.header {
  background: transparent;
  padding: 1rem 2rem;
  position: relative;
  z-index: 20;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  height: 80px;
}

.logo {
  height: 300px;
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
  position: relative;
  left: 0;
  transform: scale(0.7);
  transform-origin: left center;
}

.logo-image {
  height: 100%;
  width: auto;
  object-fit: contain;
  filter: brightness(0) invert(1);
  opacity: 1;
  padding: 1rem 0;
}

.header-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.connect-wallet {
  background: var(--primary-gradient);
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  color: var(--background-dark);
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.connect-wallet:hover {
  transform: translateY(-2px);
} 